.ranking-board {
  padding: get-unit(4);
  border-radius: get-unit(1);
  width: 50vw;
  min-width: 500px;
  position: relative;
  margin-bottom: get-unit(4);

  &__row {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    min-height: 50px;
    background-color: get-color(white);
    border-radius: get-unit(1);
    margin: get-unit(2 0);
    padding: get-unit(2);
    text-align: center;
    position: absolute;
    right: 0;
    top: 0;
    animation: fade-in 2s;
    transition: all 1500ms;
  }

  &__cell-crown {
    min-width: 50px;
    text-align: center;
  }

  &__crown-icon {
    font-size: 3em;
    color: get-color(yellow);
    animation: fade-in 500ms;
  }

  &__cell-position {
    font-size: 2em;
    padding: get-unit(0 3);
  }

  &__cell-player {
    display: flex;
    flex-grow: 1;
    align-items: center;
  }

  &__name {
    font-size: 1.5em;
    text-transform: uppercase;
    margin: get-unit(0 3);
  }

  &__cell-points {
    font-size: 1.5em;
    min-width: 30px;
    color: get-color(gray);
  }

  &__cell-gained-points {
    display: flex;
  }

  &__gained-point {
    font-size: 1em;
    min-width: 2em;
    border-radius: get-unit(8);
    background-color: get-color(gray, 1);
    padding: get-unit(1);
    align-self: flex-end;
    margin: get-unit(0 2);
    opacity: 0;
    animation: fade-in 3000ms forwards;

    &--plus {
      background-color: get-color(yellow, 3);
    }

    &--minus {
      background-color: get-color(red, 3);
    }

    &--0 {
      animation-delay: 0ms;
    }

    &--1 {
      animation-delay: 300ms;
    }

    &--2 {
      animation-delay: 600ms;
    }
  }

  &__cell-points-total {
    font-size: 1.5em;
    min-width: 50px;
    padding: get-unit(0 3);
    font-weight: bolder;
    transform: scale(1.5);
    animation: fade-in 500ms;
  }
}
